    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
	font-family: sans-serif;
	text-align: center;
}
body {
	margin: 0;
	padding: 0;
}

.container {
	display: flex;
	-webkit-columns: 2;
	   -moz-columns: 2;
	        columns: 2;
	min-height: 100vh;
}
.left {
	background: green;
	display: inline-flex;
	width: 90%;
	position: relative;
}
.right {
	background: grey;
	display: inline-flex;
	width: 10%;
}

.background-map {
	background: url(/js-screenshot/static/media/terrorussia.aa272b13.png); /* whole background */
	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-position: center;
}

.field-borders {
	width: 180px;
  margin: 0 auto;
  margin-top: 15px;
  height: 69%;
  right: -190px;
  top: 250px;
  position: absolute;
}
.penance-cont {
	width: 90px;

	display: inline-block;
	text-align: center;
	height: auto;
	box-sizing: border-box;
}
.penance-image {
	height: 60px;
	width: 60px;
	margin: 2px auto;
	border: 2px solid white;
	border-radius: 50%;
	text-align: center;
}
.penance-name {
	font-weight: bold;
	display: block;
	width: 100%;
	color: black;
	font-size: "16pt";
	font-family: "Barlow", "Helvetica Neue", sans-serif;
}

.animated-box {
  margin-left:50px;
  height: 80px;
  font-family: 'Arial';
  color: #ffffff;
  padding: 30px;
  text-align: center;
  border-radius: 8px;
  background-color:black;
}

.animated-box h1 {
  font-weight: 800;
  font-size: 14px;
  text-transform: uppercase;
}

.animated-box p {
  font-weight: 500;
}

/* The animation starts here */
.animated-box {
  position: relative;
}

.animated-box:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 4px;
  background: -webkit-linear-gradient(330deg, #ff0404, #ffb04d, #7e0e92);
  background: linear-gradient(120deg, #ff0404, #ffb04d, #7e0e92);
  background-size: 300% 300%;
  -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), 8px calc(100% - 8px), 8px 100%, 100% 100%, 100% 0%, 0% 0%);
          clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), 8px calc(100% - 8px), 8px 100%, 100% 100%, 100% 0%, 0% 0%);
}

.animated-box.in:after {
  -webkit-animation: frame-enter 1s forwards ease-in-out reverse, gradient-animation 4s ease-in-out infinite;
          animation: frame-enter 1s forwards ease-in-out reverse, gradient-animation 4s ease-in-out infinite;
}

/* motion */
@-webkit-keyframes gradient-animation {
  0% {
    background-position: 15% 0%;
  }
  50% {
    background-position: 85% 100%;
  }
  100% {
    background-position: 15% 0%;
  }
}
@keyframes gradient-animation {
  0% {
    background-position: 15% 0%;
  }
  50% {
    background-position: 85% 100%;
  }
  100% {
    background-position: 15% 0%;
  }
}

@-webkit-keyframes frame-enter {
  0% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), 8px calc(100% - 8px), 8px 100%, 100% 100%, 100% 0%, 0% 0%);
            clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), 8px calc(100% - 8px), 8px 100%, 100% 100%, 100% 0%, 0% 0%);
  }
  25% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) 100%, 100% 100%, 100% 0%, 0% 0%);
            clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) 100%, 100% 100%, 100% 0%, 0% 0%);
  }
  50% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, 100% 0%, 0% 0%);
            clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, calc(100% - 8px) 8px, 100% 0%, 0% 0%);
  }
  75% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, 8px 8px, 8px 8px, 8px 8px, 8px 8px, 8px 8px, 8px 0%, 0% 0%);
  }
  100% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 100%, 8px 100%, 8px 100%, 8px 100%, 8px 100%, 8px 100%, 8px 100%, 0% 100%);
  }
}

@keyframes frame-enter {
  0% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), 8px calc(100% - 8px), 8px 100%, 100% 100%, 100% 0%, 0% 0%);
            clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), 8px calc(100% - 8px), 8px 100%, 100% 100%, 100% 0%, 0% 0%);
  }
  25% {
    -webkit-clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) 100%, 100% 100%, 100% 0%, 0% 0%);
            clip-path: polygon(0% 100%, 8px 100%, 8px 8px, calc(100% - 8px) 8px, calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) calc(100% - 8px), calc(100% - 8px) 100%, 100% 100%, 100% 0%, 0% 0%);
  }
  50% {
